	module.exports = {"data":{"markdownRemark":{"headings":[],"html":"<p>As a student, it’s important to get access to healthy food, in order to perform better, but with the price of food rising at an incredible rate, sometimes it can be hard to make those healthy decisions. Toronto was a place of interest to me, as I am a Toronto native, and as I was researching statistics about food and diabetes, I stumbled upon the Urban Health Equity Assessment and Response Tool Toronto (Urban HEART) study. This study covers many different aspects of health equity, with the goal to promote it throughout different sectors to make health and social equity a reality. The data they had collected was extensive and in-depth, which helped me further develop the idea to promote green infrastructure, instead of just food security since the data for Toronto was not as clear. I felt that the data was more compelling when you compared mental health, Walkscore and green space, as well as diabetes, and the average amount of healthy food stores within walking distance.</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 66.66666666666666%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Health at a glance; Pie charts showing the percentage of diabetes and mental health by select neighbourhoods in Toronto.\"\n        title=\"\"\n        src=\"/static/hoodhealth2-df2b75e2491d5b408642227192d30a0a-755ae.jpg\"\n        srcset=\"/static/hoodhealth2-df2b75e2491d5b408642227192d30a0a-f07fc.jpg 125w,\n/static/hoodhealth2-df2b75e2491d5b408642227192d30a0a-9d06f.jpg 250w,\n/static/hoodhealth2-df2b75e2491d5b408642227192d30a0a-755ae.jpg 500w,\n/static/hoodhealth2-df2b75e2491d5b408642227192d30a0a-63ca3.jpg 750w,\n/static/hoodhealth2-df2b75e2491d5b408642227192d30a0a-9b9a3.jpg 1000w,\n/static/hoodhealth2-df2b75e2491d5b408642227192d30a0a-6e031.jpg 1500w,\n/static/hoodhealth2-df2b75e2491d5b408642227192d30a0a-05c45.jpg 4200w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>‘Hood Health is a project centered around the low-income neighbourhoods in Toronto, and how their health was being affected by their environment. My solution was a proposal pamphlet aimed to raise awareness about green infrastructure among residents to help improve their health and well being. A lot of the major changes in the city seem to happen around the popular, or more wealthy neighbourhoods, without much thought to the low-income neighbourhoods. If green infrastructure can help low-income neighbourhoods improve their overall health and wellness, then it would be an incentive for the rest of the city to make these changes too.</p>\n<p><img src=\"../assets/hoodhealth3.jpg\" alt=\"Your environment affects you more than you think; an abstract map of Toronto with pie charts showing the rate of low-income households in select neighbourhoods. \"></p>","frontmatter":{"title":"Hood Health","graduate":"Victoria Cake","category":"Information Design","category2":null,"category3":null,"image":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1.5,"src":"/static/hoodhealth1-0df6a8e4801093ff356e20a67141c0d9-a7a62.jpg","srcSet":"/static/hoodhealth1-0df6a8e4801093ff356e20a67141c0d9-5a1a9.jpg 375w,\n/static/hoodhealth1-0df6a8e4801093ff356e20a67141c0d9-0908c.jpg 750w,\n/static/hoodhealth1-0df6a8e4801093ff356e20a67141c0d9-a7a62.jpg 1500w,\n/static/hoodhealth1-0df6a8e4801093ff356e20a67141c0d9-83d7d.jpg 2000w","sizes":"(max-width: 1500px) 100vw, 1500px"}}}}}},"pathContext":{"slug":"/work/hood-health/","name":"/Hood Health/"}}

